import React from 'react';
import { HashRouter, Route, Link } from 'react-router-dom';

const Home = () => <h1>Home</h1>;
const About = () => <h1>About</h1>

function App() {
  return (
    <HashRouter>
      <div className="App">

        <Link to="/">Home</Link>
        <Link to="/about">About</Link>

        <Route exact path="/"><Home /></Route>
        <Route path="/about"><About /></Route>
        Test content
      </div>
    </HashRouter>
  );
}

export default App;
